import React from "react";
import "../styles.css";

const Ci = () => {
  return (
    <div className="formula">
      <input type="text" placeholder="P" />
      <span>(1+</span>
      <input type="text" placeholder="r" />
      <span>/</span>
      <input type="text" placeholder="n" />
      <span>)</span>
      <input type="text" placeholder="t" />
      <button> = </button>
      <input type="text" placeholder="A" />
    </div>
  );
};

export default Ci;
